.default {
    padding: 10px;
    border: none;
    background-color: rgba(128, 128, 128, 0.23);
}

.default:hover {
    cursor: pointer;
    box-shadow: inset 0 0 15px rgb(228 228 228);
    color: #1f3e4d;
}

.red {
    padding: 10px;
    border: none;
    background: rgba(253, 11, 11, 0.58);
}

